type Branding = { logoUrl?: string; }; const g = global as any; if (!g.__BRANDING__) g.__BRANDING__ = {} as Record<string,Branding>; export function getBranding(tenant:string|undefined){ return (g.__BRANDING__[tenant||'default']) || {}; } export function setBranding(tenant:string|undefined, b:Branding){ g.__BRANDING__[tenant||'default'] = { ...(g.__BRANDING__[tenant||'default']||{}), ...b }; return g.__BRANDING__[tenant||'default']; }
